@keyframes move-twink-back {
    from {background-position:0 0;}
    to {background-position:-10000px 5000px;}
}
@-webkit-keyframes move-twink-back {
    from {background-position:0 0;}
    to {background-position:-10000px 5000px;}
}

    .stars {
        /* position:absolute; */
        display: flex;
        margin: 0;
        padding: 0;
        
        width:100vw;
        height:100vh;
        display:block;
        background:transparent url(http://3.bp.blogspot.com/-FwB8aSiMBmI/U4t9MkimRrI/AAAAAAAAMK0/FAlqX8ZFyu8/s1600/twinkling.png) repeat top center;
        z-index:-2;
        -webkit-animation:move-twink-back 300s linear infinite;
        animation:move-twink-back 300s linear infinite;
    }
    
    h3 {
